.page-goods {
  @include media-tablet-landscape-up {
    display: grid;
  }

  @include media-tablet-landscape-only {
    grid-template-columns: 100%;

    &__promo {
      grid-row: 2;
    }
  }

  @include media-desktop-up {
    // Ð² ÑÑ‚Ð¾Ð¼ Ð²Ð°Ñ€Ð¸Ð°Ð½Ñ‚Ðµ (grid-template) ÐµÑÑ‚ÑŒ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚ÑŒ
    // ÑƒÑÑ‚Ð°Ð½Ð¾Ð²Ð¸Ñ‚ÑŒ Ð²Ñ‹ÑÐ¾Ñ‚Ñƒ row Ð¸Ð¼ÐµÐ½Ð¾Ð²Ð°Ð½Ð½Ð¾Ð¹ Ð¾Ð±Ð»Ð°ÑÑ‚Ð¸
    // ÑÑ‚Ð¾Ñ‚ ÐºÐ¾Ð´:
    // grid-template-rows: repeat(6, auto) 1fr; Ñ‚Ð°ÐºÐ¾Ð¹ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚Ð¸ Ð½Ðµ Ð´Ð°ÐµÑ‚
    grid-template:
      "breadcrumbs breadcrumbs"
      "promo promo"
      "header header"
      "filter category-list"
      "filter often-looking-top"
      "filter minicardList" 1fr
      ". often-looking-bottom"
      "brands brands"
      "admin-content admin-content"
      / #{$space*19} 1fr;

    column-gap: $space*2;
    align-items: start;


    // ! Ñ‚Ð¾Ð»ÑŒÐºÐ¾ ÑÐ²Ð¾Ð¹ÑÑ‚Ð²Ð°, Ð¾Ñ‚Ð½Ð¾ÑÑÑ‰Ð¸ÐµÑÑ Ðº grid
    // Ð´Ð»Ñ Ð½Ð¾Ð²Ñ‹Ñ… Ð¿Ð¾ÑÐ²Ð»ÑÑŽÑ‰Ð¸Ñ…ÑÑ Ð±Ð»Ð¾ÐºÐ¾Ð², Ñ‡Ñ‚Ð¾ Ð±Ñ‹ Ð±Ð»Ð¾Ðº Ð½Ðµ
    // Ð¿Ð¾Ð·Ð¸Ñ†Ð¸Ð¾Ð½Ð¸Ñ€Ð¾Ð²Ð°Ð»ÑÑ Ð² Ð¿ÑƒÑÑ‚Ð¾Ñ‚Ñ‹
    // & > * {
    //   grid-column: 1/-1;
    // }

    &__breadcrumbs {
      grid-area: breadcrumbs;
    }

    &__title-container {
      grid-area: header;
    }

    &__category-list {
      grid-area: category-list;
    }

    &__often-looking {
      grid-area: often-looking-top;
    }

    &__minicard-list {
      grid-area: minicardList;
    }

    &__often-looking-bottom {
      grid-area: often-looking-bottom;
    }

    &__brands {
      grid-area: brands;
    }

    &__admin-content {
      grid-area: admin-content;
    }

    &__promo {
      grid-area: promo;
    }

    &__filter {
      grid-area: filter;
    }
  }
}


.page-goods__title-container {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  margin: 0 0 $space*2 $space*-1;

  @include media-mobile-only {
    margin-bottom: $space*1.5;
  }
}

.page-goods__title {
  margin: 0 0 0 $space;
}

.page-goods__subtitle {
  margin-left: $space;
  color: $color-font__secondary;
}


.page-goods__category-list {
  margin-bottom: $space*1.5;
}


.page-goods__often-looking {
  margin-bottom: $space*1.5;
}


.page-goods__minicard-list {
  margin-bottom: $space*3;
}


.page-goods__often-looking-bottom {
  margin-bottom: $space*3;
}

.page-goods__brands {
  margin-bottom: $space*3;
}

.page-goods__admin-content {
  @include media-mobile-only {
    margin-bottom: $space*3;
  }
}

.page-goods__promo {
  @include media-tablet-landscape-up {
    margin-top: $space;
    margin-bottom: $space*3;
  }
}

.page-goods__banner {
  --image-height: 240px;
}

.page-goods__banner-offer {
  @include media-mobile-only {
    position: static;
    transform: none;
    max-width: none;
    padding: $space 0;
    text-align: center;
  }
}

.page-goods__filter {
  @include media-tablet-landscape-down {
    display: none;
  }
}
