@keyframes showModal {
  from {
    transform: scale(0.8);
  }

  to {
    transform: scale(1);
  }
}

@keyframes showModal-opacity-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes hideModal {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0.9);
  }
}


.modal {
  --vertical-paddings: #{$space * 2};
  --horizontal-paddings: #{$space * 2};

  position: fixed;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: $color-font__base;
}

.modal:not(.modal--bounded) {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.modal--center {
  padding: $space*5 0 $space*5; // Ð§Ñ‚Ð¾Ð±Ñ‹ Ð¾ÐºÐ½Ð¾ Ð½Ðµ Ð¿Ñ€Ð¸Ð»Ð¸Ð¿Ð°Ð»Ð¾ Ðº Ð³Ñ€Ð°Ð½Ð¸Ñ†Ðµ Ð±Ñ€Ð°ÑƒÐ·ÐµÑ€Ð°
}


.modal--bounded {
  position: absolute;
  top: 100%;
  max-width: 100vw;
  width: 500px;
  padding: var(--vertical-paddings) var(--horizontal-paddings);
  background-color: $color__background;
  border-radius: $controls__border-radius;
  box-shadow: 0 3px 6px 0 $color__shadows;
}

// TODO Ð¿Ñ€Ð¾Ð²ÐµÑ€Ð¸Ñ‚ÑŒ Ð½ÐµÐ¾Ð±Ñ…Ð¾Ð´Ð¸Ð¼Ð¾ÑÑ‚ÑŒ Ñ ÑÐ²ÐµÑ‚Ðµ .modal__content--closed
// upd - Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÑ‚ÑÑ Ð² modal--bounded
.modal--closed {
  display: none;
}

.modal__inner {
  position: relative;
  z-index: 2;
  width: 500px;
  margin-right: auto;
  background-color: $color__background;
  border-radius: $controls__border-radius;
  box-shadow: 0 3px 6px 0 $color__shadows;
  animation:
    showModal 150ms cubic-bezier(0, 0, 0.2, 1) both,
    showModal-opacity-in 40ms linear both;
}

.modal--center .modal__inner {
  max-width: 100%;
  // margin: auto; Ð¿Ñ€Ð¸ Ð¶ÐµÐ»Ð°Ð½Ð¸Ð¸ Ð²Ñ‹Ñ€Ð°Ð²Ð½Ð¸Ð²Ð°Ð½Ð¸Ñ Ð²Ð¾ Ð²ÐµÑ€Ñ‚Ð¸ÐºÐ°Ð»Ð¸
  margin-left: auto;
}

.modal--left .modal__inner {
  // $space*4 - ÑˆÐ¸Ñ€Ð¸Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÐ¸
  max-width: calc(100% - #{$space*4});
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  transform-origin: left top;
}

.modal--small .modal__inner {
  width: $space*22;
}

.modal--big .modal__inner {
  width: $space*44;
}


.modal--animation-on-close .modal__inner {
  animation: hideModal 75ms linear both;
}

.modal--animation-on-close .modal__overlay {
  animation: hideOverlay 75ms linear both;
}


.modal__content {
  padding: var(--vertical-paddings) var(--horizontal-paddings);
}

.modal__content--closed {
  display: none;
}


.modal__close-button {
  position: absolute;
  color: #ffffff;
  border-radius: $controls__border-radius;

  &:hover,
  &:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.modal--center .modal__close-button {
  @include media-mobile-only {
    top: $space * -4.25;
    right: 0;
  }

  @include media-tablet-portrait-up {
    top: 0;
    right: $space * -4.25;
  }
}

.modal--left .modal__close-button {
  top: 0;

  @include media-mobile-only {
    right: $space*-4;
  }

  @include media-tablet-portrait-up {
    right: $space*-4.25;
  }
}


.modal__header {
  margin: 0 0 $space 0;
  font-size: 1.7rem;
  line-height: 2rem;
  font-weight: 600;
}


.modal__p {
  margin: 0;

  & + .modal__p {
    margin-top: $space*0.5;
  }
}

.modal__element {
  margin-bottom: $space;
}


.modal__parent-bound {
  position: relative;
  z-index: 2;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    width: 0;
    height: 0;
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-bottom: 9px solid $color__background;
  }
}


@import "modal-city";
@import "modal-signin";
@import "modal-register";
@import "modal-remind";
