@import "details";
@import "buy-card";


.page-product {
  @include media-tablet-landscape-up {
    display: grid;
    // Ð² ÑÑ‚Ð¾Ð¼ Ð²Ð°Ñ€Ð¸Ð°Ð½Ñ‚Ðµ (grid-template) ÐµÑÑ‚ÑŒ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚ÑŒ
    // ÑƒÑÑ‚Ð°Ð½Ð¾Ð²Ð¸Ñ‚ÑŒ Ð²Ñ‹ÑÐ¾Ñ‚Ñƒ row Ð¸Ð¼ÐµÐ½Ð¾Ð²Ð°Ð½Ð½Ð¾Ð¹ Ð¾Ð±Ð»Ð°ÑÑ‚Ð¸
    // ÑÑ‚Ð¾Ñ‚ ÐºÐ¾Ð´:
    // grid-template-rows: repeat(6, auto) 1fr; Ñ‚Ð°ÐºÐ¾Ð¹ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚Ð¸ Ð½Ðµ Ð´Ð°ÐµÑ‚
    grid-template:
      "breadcrumbs breadcrumbs"
      "header offer"
      "gallery offer"
      "details offer"
      "details buy-card" 1fr
      "similar similar"
      "viewed viewed"
      / 2fr 1fr;

    column-gap: $space*3;
    align-items: start;


    // ! Ñ‚Ð¾Ð»ÑŒÐºÐ¾ ÑÐ²Ð¾Ð¹ÑÑ‚Ð²Ð°, Ð¾Ñ‚Ð½Ð¾ÑÑÑ‰Ð¸ÐµÑÑ Ðº grid
    // Ð´Ð»Ñ Ð½Ð¾Ð²Ñ‹Ñ… Ð¿Ð¾ÑÐ²Ð»ÑÑŽÑ‰Ð¸Ñ…ÑÑ Ð±Ð»Ð¾ÐºÐ¾Ð², Ñ‡Ñ‚Ð¾ Ð±Ñ‹ Ð±Ð»Ð¾Ðº Ð½Ðµ
    // Ð¿Ð¾Ð·Ð¸Ñ†Ð¸Ð¾Ð½Ð¸Ñ€Ð¾Ð²Ð°Ð»ÑÑ Ð² Ð¿ÑƒÑÑ‚Ð¾Ñ‚Ñ‹
    // & > * {
    //   grid-column: 1/-1;
    // }

    &__breadcrumbs {
      grid-area: breadcrumbs;
    }

    &__header {
      grid-area: header;
    }

    &__gallery {
      grid-area: gallery;
    }

    &__offer {
      grid-area: offer;
    }

    &__details {
      grid-area: details;
    }

    &__buy-card {
      grid-area: buy-card;
    }

    &__similar {
      grid-area: similar;
    }

    &__viewed {
      grid-area: viewed;
    }
  }
}

.page-product__breadcrumbs {
  margin-bottom: $space;
}


.page-product__header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: $space*0.5;

  @include media-tablet-portrait-down {
    margin-bottom: $space*0.5;
  }

  @include media-tablet-landscape-up {
    margin-bottom: $space;
  }
}


.page-product__title {
  margin: 0 0 $space;
  width: 100%;
}


.page-product__code-caption {
  color: $color-font__secondary;

  &--short {
    @include media-tablet-portrait-up {
      display: none;
    }
  }

  &--full {
    @include media-mobile-only {
      display: none;
    }
  }
}


.page-product__code {
  font-weight: 600;
}


.page-product__gallery {
  @include media-tablet-portrait-down {
    padding-bottom: $space;
  }

  @include media-tablet-landscape-up {
    padding-bottom: $space*4;
  }
}


.page-product__offer {
  @include media-mobile-only {
    margin-bottom: $space;
  }

  @include media-tablet-portrait-up {
    margin-bottom: $space*3;
  }
}

.page-product__details {
  margin-bottom: $space*4;
  padding-top: $space*2;
  border-top: 1px solid $color__additional-2;
}

.page-product__buy-card {
  @include media-tablet-portrait-down {
    display: none;
  }
}
